body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    float: left;
    font-size: 24px; 
     font-weight: bold; 
     line-height: 34px;
    height: 34px;
     margin-right: -1px;
    margin-top: -1px; 
     padding: 0; 
     text-align: center; 
    width: 34px;
  }
  .square:hover{
    background-color: rgb(245,234,220);
  }
  .game {
    display: flex;
    flex-direction: row;
  }
  